import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

function useIndexPath() {
  const { markdownRemark } = useStaticQuery(graphql`
    query IndexPage {
      markdownRemark(frontmatter: { index: { eq: true } }) {
        fields {
          slug
        }
      }
    }
  `);

  return markdownRemark ? markdownRemark.fields.slug : '/';
}

export default function withIndexPath(WrapedComponent) {
  return props => {
    const indexPath = useIndexPath();
    return <WrapedComponent indexPath={indexPath} {...props} />;
  };
}
